@import url(https://fonts.googleapis.com/css2?family=Patrick+Hand&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Quicksand&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

* {
  padding: 0;
  border: 0;
  box-sizing: 0;
  font-family: "Quicksand", sans-serif;
}
body {
  text-align: center;
  height: 100vh;
}
header,
footer {
  padding: 0.4rem 10%;
  background-color: rgb(202, 234, 255);
  display: flex;
  align-items: center;
  justify-content: space-around;
}
footer {
  position: sticky;
  bottom: 0;
}

select,
button {
  text-align: center;
  height: 2rem;
  margin: 0 1rem;
  padding: 0 1rem;
  background-color: white;
  border: 1px solid black;
}

.ind_card {
  width: 20%;
  border: 2px solid black;
  border-radius: 10%;
  padding: 1.5em;
  margin: 1rem;
  text-align: center;
  font-weight: bold;
  box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.274);
  background-color: white;
}
.ind_card img {
  width: 100%;
}

#game_over {
  height: 100vh;
}
#game_over h2 {
  font-size: 4em;
  padding: 2rem;
  color: red;
}

.game_board {
  display: flex;
  max-width: 80vw;
  margin: auto;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
.game_header {
  position: sticky;
  top: 0;
  background-color: white;
  padding: 0.2rem;
}

